.check-button input {
  opacity: 0;
  width: 0;
  height: 0;
}

.checkmark {
  background-color: rgba(96, 97, 96, .4);
  border-radius: 4px;
  position: absolute;
  top: 5px;
  bottom: 0;
  left: 5px;
  right: 0;
}

.check-button input:checked + .checkmark {
  background-color: #ae1332;
}

.checkmark:after {
  content: "";
  display: none;
  position: absolute;
}

.check-button input:checked + .checkmark:after {
  display: block;
}

.check-button .checkmark:after {
  width: 5px;
  height: 10px;
  border: 2px solid #fff;
  border-width: 0 2px 2px 0;
